import React from 'react'
import { withRouter } from 'react-router';
import {connect} from 'react-redux'
import * as loginActions from '../../redux/actions/loginActionCreators'
import { bindActionCreators } from "redux"
import { getloginState } from '../../redux/selectors/loginSelector'
const logoImg = require('../../static/img/logo.png')
const menus = [
    // {
    //     title: '????????????',
    //     key: '/timeMonitor',
    //     icon:'icon-assessment-24px'
    // },
    {
        title: '????????????',
        key: '/authority/userManage',
        icon:'icon-build-24px'
    },
    {
        title: 'token????????????',
        key: '/token',
        icon:'icon-securitytoken-24px'
    }
]

class CustomMenu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            menuKey:0
        }
    }
    tabMenu = (item,index)=> {
        this.setState({menuKey:index},this.props.history.push(item.key))
    }
    renderMenuItem = (item, index) => {
        const {title} = item;
        const pathname = window.location.pathname
        const isPath = pathname.indexOf('/authority') > -1 && index===0
        return (
            <li onClick={()=>this.tabMenu(item,index)} key={index} className={pathname.indexOf(item.key) > -1 || isPath?'menu-li active animated slideInLeft':'menu-li no-active'}>
                <i className={'icon-iconmoon '+item.icon}></i>
                <p>{title}</p>
            </li>
        )
    }
    render() {
        const moduleRoute =['/authority/userManage','/authority/roleManage','/authority/authority','/token']
        const pathname = window.location.pathname
        const IndexO = moduleRoute.indexOf(pathname)>-1
        return (
            IndexO &&
            <div className="menus">
                <div>
                    <img alt="logo" title="??????????????????" height={32} className="logo" style={{ verticalAlign: 'middle' }} src={logoImg} />
                </div>
                <ul>
                {
                    menus.map((item,index) => {
                        const {user} = this.props.userInfo
                        if(user && (index==menus.length-1) && user.organization!=='SYSTEM'){
                            return  null
                        }
                        return  this.renderMenuItem(item,index)
                    })
                }
                </ul>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    const userInfo = getloginState(state)
    return {
        userInfo
    }
}
//??????Redux actions??????????????????
const mapDispatchToProps = (dispatch) => {
    return bindActionCreators(loginActions, dispatch)
}
//????????????
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(CustomMenu))
