import React, { PureComponent } from 'react';
import request from 'util/http'
import { formatterYMD, filterUrl } from 'util/index'
import { Select, Button, Table, Icon, message, Form, Drawer, Input, Popconfirm, Empty } from 'antd';
import Mock from 'mockjs'
import AddUser from '../../component/addUser'
import {connect} from 'react-redux'
import * as loginActions from '../../../redux/actions/loginActionCreators'
import { bindActionCreators } from "redux"
import { getloginState } from '../../../redux/selectors/loginSelector'
import Header from 'components/common/Header'
const { Option } = Select;

// ???????????????????????????????????????????????????????????????
class FilterForm extends React.Component {
    submit = () => {  // ??????????????????????????????
        const { form, parent } = this.props;
        parent.getFilterFormMsg(this, form.getFieldsValue())
    };
    addUser = (data,value)=> {
        this.props.parent.props.setOpenState(data,value)
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const { codeList } = this.props;
        // ????????????option??????
        const defaultItem = [<Option key='' value="">??????</Option>];
        const codes = defaultItem.concat( codeList.map( (item,index) => { return <Option key={index} value={item}>{ item }</Option> } ) );
        return (
            <div className="dis-flex">
                <Button className="filter-add" type="primary" icon="plus" onClick={()=>this.addUser(null,1)}>????????????</Button>
                <Form layout="inline" className="default-form">
                    {/* ??????????????????????????? */}
                    <Form.Item label="">
                        {getFieldDecorator('organization',{
                            rules: [{ required: false, message: '' }],
                        })(
                            <Select
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                placeholder="???????????????????????????" style={{ width: '200px' }}>{ codes }</Select>
                        )}
                    </Form.Item>
                    <Form.Item label="">
                        {getFieldDecorator('username', {
                            rules: [{ required: false, message: '' }],
                        })(<Input placeholder="??????????????????????????????"/>)}
                    </Form.Item>
                    <Button onClick={this.submit} className="filter-submit" type="primary" icon="search">??????</Button>
                </Form>
            </div>
        );
    }
}
const FilterFormBox =Form.create({ name: 'normal_login' })(FilterForm)

class User extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            loading: false,  // table loading
            data: [],       // ????????????
            codeList: [], // ????????????
            columns: [
                {
                    title: '?????????',
                    dataIndex: 'username',
                    render: (text,row) => {
                        const colors = ['#C8E6C9','#3C97CE','#BED2DC','#F0E71C','#FF767C']
                        const random = row.id%5
                        return <div className="text-hover username" onClick={() => this.getDetail(row)}><img style={{borderRadius:10,marginRight:10}} src={Mock.Random.image('20x20', colors[random], '#FFF', 'png', row.username&&row.username.slice(0,1).toUpperCase())} alt=""/><span>{text}</span></div>
                    }
                },
                {
                    title: '??????',
                    dataIndex: 'email',
                },
                {
                    title: '????????????',
                    dataIndex: ' ',
                    render: (text,row) => <div className="text-hover created_by">
                        {
                            row.roleList.length>0 && row.roleList.map((item,index)=>{
                                return row.roleList.length==index+1?item.name:`${item.name},`
                            })
                        }
                    </div>
                },
                {
                    title: '??????',
                    dataIndex: 'organization',
                },
                {
                    title: 'org_id',
                    dataIndex: 'orgId',
                },
                {
                    title: '??????',
                    dataIndex: '',
                    width:200,
                    render: (text,row) => <div className="table-btn dis-flex">
                        <span className="btn" onClick={()=>this.addUser(row,2)}>????????????</span>
                        <span className="btn" onClick={()=>this.addUser(row,3)}>
                            <i className="icon-iconmoon icon-Edit_Pencil-24px cursor"></i>
                        </span>

                        <Popconfirm
                            icon={null}
                            title="???????????????????????????????????????????????????????"
                            onConfirm={() => this.deleteUser(row)}
                            okText="??????"
                            cancelText="??????"
                        >
                            <span className="btn"><i className='icon-iconmoon icon-delete_trashcan-24px cursor cursor'></i></span>
                        </Popconfirm>
                    </div>,
                }
            ],
            visible: false,
        }
    }
    //????????????
    deleteUser = (row)=> {
        request({
            url: `/user/deleteUser?id=${row.id}`,
            method: 'DELETE',
            success: res => {
                if(!res || res.code !== 0) {
                    return
                }
                message.success('???????????????')
                this.getUserList({})
            },
            error: () => {
                message.error('???????????????')
                this.setState({ loading: false })
            }
        })
    }
    addUser = (data,openType)=> {
        // 1????????????2??????????????????3?????????
        this.props.setOpenState(data,openType)
    }
    onClose = () => {
        this.setState({
            visible: false,
        });
    };
    getFilterFormMsg(child, formData) {
        this.getUserList(formData)
    }
    getUserList(data) {
        let url = ''
        if(data) {
            for(let key in data) {
                const item = data[key];
                if(item && item !== 'all') {
                    const symbol = ( url === '' ? '?' : '&' );
                    url += ( symbol + key + '=' + item )
                }
            }
        }

        this.setState({ loading: true })
        const organization = data.organization?`organization=${data.organization}`:''
        const email = data.username?`&email=${data.username}`:''
        const username = data.username?`&username=${data.username}`:''
        const pageNum = `&pageNum=1`
        const pageSize = `&pageSize=10`
        const pams = filterUrl(`${organization}${email}${username}${pageNum}${pageSize}`)
        request({
            url: `/user/getUserByCondition?${pams}`,
            method: 'GET',
            success: res => {
                if(!res || res.code !== 0) {
                    return
                }
                let filterList = res.data&&res.data.userList
                const {user} = this.props.userInfo
                if(user.organization!=='SYSTEM'){
                    filterList=filterList.filter((item,index)=>item.user.organization!=='SYSTEM')
                }
                this.setState({ data: filterList.map( (item, key) =>{
                    const newItem={}
                    newItem.key=key;
                    newItem.roleList=item.roleList;
                    return {...newItem,...item.user}
                }), loading: false  })
            },
            error: () => {
                message.error('???????????????')
                this.setState({ loading: false })
            }
        })
    }
    // ??????????????????
    getOrganizationList() {
        request({
            url: `/user/getOrganization`,
            method: 'GET',
            success: res => {
                if(!res || res.code !== 0) {
                    return
                }
                this.setState({ codeList: res.data })
            },
            error: () => {
                message.error('???????????????')
            }
        })
    }
    componentDidMount() {
        this.getOrganizationList();    // ??????????????????
        this.getUserList({});
    }
    //?????????????????????????????????
    getDetail = (row)=> {
        const colors = ['#C8E6C9','#3C97CE','#BED2DC','#F0E71C','#FF767C']
        const random = row.id%5
        row.imgUrl=Mock.Random.image('20x20', colors[random], '#FFF', 'png', row.username&&row.username.slice(0,1).toUpperCase())
        const pageNum = `&pageNum=1`
        const pageSize = `&pageSize=10`
        request({
            url: `/user/getRoleAndAuthById?id=${row.id}${pageNum}${pageSize}`,
            method: 'GET',
            success: res => {
                if(!res || res.code !== 0) {
                    return
                }
                const data = res.data
                data.roleDto.map((item,index)=>{
                    const itm = item
                    itm.key=index
                    return itm
                })
                this.setState({drawerData:{...data,row},visible:true})
            },
            error: () => {
                message.error('???????????????')
                this.setState({ loading: false })
            }
        })
    }
    filterRoleDto = (e)=> {
        this.setState({filterRoleDto:e.target.value})
    }
    render() {
        const {columns, data, loading, codeList, drawerData} = this.state;
        const header = drawerData && drawerData.row
        const roleDto = drawerData && drawerData.roleDto
        const total = drawerData && drawerData.total
        const filterRole = roleDto&&roleDto.filter((item,index)=>{
            if(!this.state.filterRoleDto || item.role.name.indexOf(this.state.filterRoleDto)>-1){
                return true
            }
        })
        const columns2 = [
            {
                title:'??????',
                dataIndex: 'role',
                key: 'role',
                width:'50%',
                render:(text,row)=><span style={{cursor:'pointer'}} onClick={()=>{}}>{row.role.name}</span>
            },
            {
                title: '??????',
                dataIndex: 'authority',
                key: 'authority',
                render:(text,row)=><span style={{color:'rgb(0, 175, 152)',cursor:'pointer'}}>{row.authList.map((item,index)=><span key={index} onClick={()=>{}}>{item.authority.length>0?item.authority+';   ':item.authority}</span>)}</span>
            },
        ];
        return (
            <div>
                <Header/>
                <div className="main-panel">
                    <div className="filter-panel">
                        <FilterFormBox parent={this} codeList={codeList}/>
                    </div>
                    <div className="table">
                        <Table
                            rowKey={record=>record.id}
                            className={data.length>0?'':'no-border'}
                            pagination={false}
                            loading={loading}
                            columns={columns}
                            dataSource={data}
                        />
                    </div>
                    <Drawer
                        width={640}
                        placement="right"
                        closable={false}
                        onClose={this.onClose}
                        visible={this.state.visible}
                        className="drawer"
                    >
                        <div className="header p-style dis-flex">
                            <img className="avatar" alt="avatar" style={{borderRadius:'50%'}} src={header && header.imgUrl} />
                            <div>
                                <div>{header && header.username}</div>
                                <div className="dis-flex"><span>{header && header.organization}</span><span>{header && header.email}</span></div>
                            </div>
                        </div>
                        <div className="tab-bar p-style"><Button>????????????</Button></div>
                        <Input
                            style={{width:'600px',margin:'10px 20px 0 20px'}}
                            placeholder="????????????????????????"
                            prefix={<Icon type="search" style={{color: 'rgba(153,153,153,1)'}} />}
                            onPressEnter={(e)=>this.filterRoleDto(e)}
                            onBlur={(e)=>this.filterRoleDto(e)}
                            allowClear={true}
                        />
                        {roleDto?
                            (roleDto.length?
                              <Table style={{padding:'0 20px 20px 20px'}} dataSource={filterRole} columns={columns2}
                                     pagination={{
                                         size:"small",
                                         total:total,
                                         pageSize:10,
                                         hideOnSinglePage:true
                                     }}
                                />
                            :
                                <Empty description={'?????????????????????'} style={{marginTop:100}}/>
                            )
                            :null
                        }
                    </Drawer>
                </div>
                {/*?????????*/}
                <AddUser width={540} height={540} refresh={()=>this.getUserList({})}/>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    const userInfo = getloginState(state)
    return {
        userInfo
    }
}
//??????Redux actions??????????????????
const mapDispatchToProps = (dispatch) => {
    return bindActionCreators(loginActions, dispatch)
}
//????????????
export default connect(mapStateToProps, mapDispatchToProps)(User)
