import React, { PureComponent } from 'react'
import {Link} from 'react-router-dom';
import { logOut } from 'util/userApi'
import { connect } from "react-redux"
import { bindActionCreators } from "redux"
import * as loginActions from 'redux/actions/loginActionCreators'
import { getloginState } from 'redux/selectors/loginSelector'
import { Popover, Modal, Menu } from 'antd'
import Setting from './setting'
import Mock from 'mockjs'
const menus = [
    {
        title: '????????????',
        key: '/authority/userManage',
        // subs: [
        //     {key: '/authority/userManage', title: '??????'},
        //     {key: '/authority/userGroupManage', title: '?????????'},
        // ]
    },
    {
        title: '????????????',
        key: '/authority/roleManage'
    },
    {
        title: '????????????',
        key: '/authority/authority'
    }
]
class Header extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            modalShow: false,  // ??????????????????
        }
    }

    handleSubmit() { // ????????????

    }

    modalControl(bool) {  // ??????????????????
        this.setState({ modalShow: bool })
    }

    renderProfilePopUp() { // ????????????popover??????
        const { userInfo } = this.props;
        const name = userInfo.user && userInfo.user.username
        const avator = Mock.Random.image('100x100', '#894FC4', '#FFF', 'png', name && name.slice(0,1).toUpperCase())
        return (
            <div className="setting-popup">
                <div className="popup-content">
                    <div className="top">
                        <img className="avatar" alt="avatar" src={avator} />
                        <div className="avatar-info">
                            <div className="user-info">{name}</div>
                            <button type="button" className="logout-button" onClick={ logOut }>????????????</button>
                        </div>
                    </div>
                    <div className="bottom">
                        <div className="setting" onClick={ () => { this.modalControl(true) } }><i className="sap-icon icon-action-settings"></i><span>??????</span></div>
                    </div>
                </div>
            </div>
        )
    }
    componentWillMount(){
        // notification.config({
        //     placement: 'bottomRight',
        //     bottom: 50,
        //     duration: 6,
        // });
    }

    renderSubMenu = ({key, title, subs}) => {
        return (
            <Menu.SubMenu key={key} title={<span>{title}<i className="icon-iconmoon icon-arrow_drop_down-24px"></i></span>}>
                {
                    subs && subs.map(item => {
                        return this.renderMenuItem(item,1)
                    })
                }
            </Menu.SubMenu>
        )
    }
    renderMenuItem = (item, num) => {
        const {key, title} = item;
        return (
            <Menu.Item key={key}  style={num == 0 ? {textAlign:'center'} : {width:'100%',textAlign:'center',height:34,lineHeight:'34px'}}>
                <Link to={key}>
                    <span>{title}</span>
                    <div className="bottom"></div>
                </Link>
            </Menu.Item>
        )
    }
    render() {
        const hash = window.location.hash.slice(2);
        const { userInfo, cleanComponent, header, headerMenu } = this.props;
        const name = userInfo.user && userInfo.user.username
        const avator = Mock.Random.image('100x100', '#894FC4', '#FFF', 'png', name && name.slice(0,1).toUpperCase())
        return (
            <header>
                <div className="top">
                    <div className="profile-content">

                    </div>
                    <span className="top-title ms-Grid-col"><i className="icon-iconmoon icon-build-24px"></i>{cleanComponent?header:'????????????'}</span>
                    <div className="profile-content">
                        <img
                            className="avatar"
                            alt="avatar"
                            src={avator} />
                        {!this.state.modalShow && <Popover
                            content={ this.renderProfilePopUp() }
                            placement="bottomRight"
                            title=""
                            trigger="click"
                        >
                            <i className="icon-iconmoon icon-arrow_drop_down-24px cursor"></i>
                        </Popover>}
                        <Modal
                            className="setting-modal"
                            title="??????"
                            width={620}
                            style={{ top: '100px'}}
                            visible={ this.state.modalShow }
                            cancelText='??????'
                            okText='??????'
                            onOk={ (e) => { this.handleSubmit() } }
                            onCancel={ (e) => { this.modalControl(false) } }
                        > <Setting userInfo={ userInfo } /> </Modal>
                    </div>
                </div>
                {headerMenu?
                    <div className="all-menus">
                        {window.location.pathname=='/'?
                            <Menu
                                defaultSelectedKeys={window.location.pathname=='/'?'/authority/userManage':[`${window.location.pathname}`]}
                                mode="horizontal"
                                className="header-navbar"
                            >
                                {
                                    headerMenu.map(item => {
                                        return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item,0)
                                    })
                                }
                            </Menu>
                            :
                            <Menu
                                defaultSelectedKeys={[`${window.location.pathname}`]}
                                mode="horizontal"
                                className="header-navbar"
                            >
                                {
                                    headerMenu.map(item => {
                                        return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item,0)
                                    })
                                }
                            </Menu>
                        }
                    </div>
                    :
                <div className="all-menus">
                    {window.location.pathname=='/'?
                        <Menu
                            defaultSelectedKeys={window.location.pathname=='/'?'/authority/userManage':[`${window.location.pathname}`]}
                            mode="horizontal"
                            className="header-navbar"
                        >
                            {
                                menus.map(item => {
                                    return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item,0)
                                })
                            }
                        </Menu>
                        :
                        <Menu
                            defaultSelectedKeys={[`${window.location.pathname}`]}
                            mode="horizontal"
                            className="header-navbar"
                        >
                            {
                                menus.map(item => {
                                    return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item,0)
                                })
                            }
                        </Menu>
                    }
                </div>
                }
            </header>
        )
    }
}

const mapStateToProps = (state) => {
    const userInfo = getloginState(state)
    return {
        userInfo
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators(loginActions, dispatch)
}

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(Header)