import { WebAuth } from 'auth0-js'
import request from 'util/http'
import {message} from 'antd'
function getQueryString(url, name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = url.substr(1).match(reg);
    if (r != null) return unescape(r[2]);
    return ''
}

function isLogin() {
    const token = getCookie('access_token')
    return Boolean(token)
}

function logOut() {
    delCookie('access_token')
    window.location.href='/'
}
function login(values) {
    delete values.remember
    const emailValues =Object.assign({},values)
    var reg = /^(\w)+(\.\w+)*@(\w)+((\.\w+)+)$/;
    const is_Email = reg.test(values.username)
    delete emailValues.username
    emailValues.email=values.username
    let organization = "SYSTEM";
    const data = is_Email?{...emailValues,...{organization}}:{...values,...{organization}}
    request({
        url: `/login`,
        method: 'POST',
        data,
        success: res => {
            if(!res || res.code !== 0) {
                return
            }
            const responses = res.data
            setCookie('login_data',JSON.stringify(responses),0.5)
            setCookie('access_token',JSON.stringify(responses.jwt.access_token),0.5)
            setCookie('refresh_token',JSON.stringify(responses.jwt.refresh_token),0.5)
            setCookie('username',JSON.stringify(responses.user.username),0.5)
            message.success('???????????????')
            window.location.href='/authority/userManage'
        },
        error: error => {
            // if(error.code === -2){
            //
            // }
            const msg = error.response && error.response.data.msg
            message.error(msg)
        }
    })
}
